.box {
  border: 4px solid grey;
  width: 250px;
  height: 250px;
  border-radius: 20%;
  margin: 40px auto;
  position: relative;
  display: flex;
  align-items: center; }
  .box__display {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    font-size: 3rem;
    border: 3px solid grey;
    border-radius: 20%; }
  .box__button {
    width: 8%;
    height: 17%;
    position: absolute;
    right: 0;
    top: 50%;
    background: grey;
    transform: translate(100%, -50%);
    border-radius: 20%; }
